.message-me {
  border: 2px solid red;
  width: 40%;

  display: flex;
  flex-direction: column;

  h1 {
    text-align: center;
  }
  input[type="submit"] {
    align-self: flex-end;
    width: 30%;
    margin: 10px 5px;
    height: 30px;
  }
  @media (max-width: 800px) {
    width: 100%;
  }
}
